.star-rating {
  font-size: 24px;
  color: #ccc;
  display: inline-block;
  cursor: pointer;
}

.star-rating .fa {
  margin-right: 5px;
}

.star-rating .fa:hover,
.star-rating .fa:hover ~ .fa {
  color: #ffcc00;
}

.star-rating.selected .fa-star-o,
.star-rating.selected .fa-star-o ~ .fa-star-o,
.star-rating.selected .fa-star,
.star-rating.selected .fa-star ~ .fa-star {
  color: #ffcc00;
}
